.loginContainer {
  display: flex;
  flex-direction: column;
  width: 50%;
  max-width: 30rem;
  h1 {
    margin: 2rem 0;
    font-family: "Montserrat";
    font-size: 3rem;
    color: white;
  }
  .loginform {
    background: #ffffff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.35);
    border-radius: 29px;
    padding: 3rem 2rem;
    display: flex;
    flex-direction: column;
    font-family: "Montserrat";
    grid-gap: 1.5rem;

    .inputPasswordContainer {
      position: relative;
      display: flex;
      background: rgba(196, 196, 196, 0.3);
      border-radius: 9px;
      font-size: 1rem;
      border: none;

      .eyeIcon {
        position: absolute;
        top: 30%;
        right: 5px;
        transform: translate(-50%, 0, 0);
      }
      .eyeIcon:hover {
        opacity: 0.6;
        cursor: pointer;
      }
    }

    .buttonForm {
      background: #ffd200;
      border-radius: 9px;
      margin: 0 auto;
      border: none;
      padding: 0.75rem 2rem;
      font-weight: 800;
      font-size: 1.5rem;
      font-family: "Montserrat";
    }
  }

  .haveAccount {
    font-family: "Montserrat";
    margin: 1rem 0;
    color: white;
    .register {
      border: none;
      border-bottom: 1px solid #ffd200 !important;
      background: none;
      font-weight: 400 !important;
      font-size: 1rem;
      color: #ffd200;
      cursor: pointer;
    }
  }

  .logo {
    height: 7rem;
    margin: 0 auto;
  }
}
